import React, { useState, useEffect } from 'react';
import {
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Button,
  Typography,
  Box,
  Grid,
  Card,
  CardContent,
} from '@mui/material';
import axios from 'axios';

interface Transaction {
  id: number;
  member_id: number;
  date: string;
  amount: number;
  transaction_type: string;
  description: string;
  reference_number: string;
}

const Accounting: React.FC = () => {
  const [transactions, setTransactions] = useState<Transaction[]>([]);

  useEffect(() => {
    const fetchTransactions = async () => {
      try {
        const response = await axios.get('http://localhost:8000/transactions/');
        setTransactions(response.data);
      } catch (error) {
        console.error('Error fetching transactions:', error);
      }
    };

    fetchTransactions();
  }, []);

  const calculateTotals = () => {
    return transactions.reduce(
      (acc, transaction) => {
        if (transaction.transaction_type === 'payment') {
          acc.totalPayments += transaction.amount;
        } else if (transaction.transaction_type === 'charge') {
          acc.totalCharges += transaction.amount;
        }
        return acc;
      },
      { totalPayments: 0, totalCharges: 0 }
    );
  };

  const { totalPayments, totalCharges } = calculateTotals();

  return (
    <div>
      <Typography variant="h4" gutterBottom>
        Accounting
      </Typography>

      <Grid container spacing={3} sx={{ mb: 3 }}>
        <Grid item xs={12} md={4}>
          <Card>
            <CardContent>
              <Typography color="textSecondary" gutterBottom>
                Total Payments
              </Typography>
              <Typography variant="h5" component="div">
                ${totalPayments.toFixed(2)}
              </Typography>
            </CardContent>
          </Card>
        </Grid>
        <Grid item xs={12} md={4}>
          <Card>
            <CardContent>
              <Typography color="textSecondary" gutterBottom>
                Total Charges
              </Typography>
              <Typography variant="h5" component="div">
                ${totalCharges.toFixed(2)}
              </Typography>
            </CardContent>
          </Card>
        </Grid>
        <Grid item xs={12} md={4}>
          <Card>
            <CardContent>
              <Typography color="textSecondary" gutterBottom>
                Balance
              </Typography>
              <Typography variant="h5" component="div">
                ${(totalPayments - totalCharges).toFixed(2)}
              </Typography>
            </CardContent>
          </Card>
        </Grid>
      </Grid>

      <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 3 }}>
        <Typography variant="h5">Transaction History</Typography>
        <Button variant="contained" color="primary">
          Record Transaction
        </Button>
      </Box>

      <TableContainer component={Paper}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>Date</TableCell>
              <TableCell>Reference</TableCell>
              <TableCell>Description</TableCell>
              <TableCell>Type</TableCell>
              <TableCell>Amount</TableCell>
              <TableCell>Actions</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {transactions.map((transaction) => (
              <TableRow key={transaction.id}>
                <TableCell>{new Date(transaction.date).toLocaleDateString()}</TableCell>
                <TableCell>{transaction.reference_number}</TableCell>
                <TableCell>{transaction.description}</TableCell>
                <TableCell>{transaction.transaction_type}</TableCell>
                <TableCell>${transaction.amount.toFixed(2)}</TableCell>
                <TableCell>
                  <Button size="small" color="primary">
                    View
                  </Button>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </div>
  );
};

export default Accounting;
