import React, { useState, useEffect } from 'react';
import {
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Button,
  Typography,
  Box,
  Chip,
} from '@mui/material';
import axios from 'axios';

interface Statement {
  id: number;
  member_id: number;
  statement_date: string;
  due_date: string;
  total_amount: number;
  paid_amount: number;
  status: string;
}

const Statements: React.FC = () => {
  const [statements, setStatements] = useState<Statement[]>([]);

  useEffect(() => {
    const fetchStatements = async () => {
      try {
        const response = await axios.get('http://localhost:8000/statements/');
        setStatements(response.data);
      } catch (error) {
        console.error('Error fetching statements:', error);
      }
    };

    fetchStatements();
  }, []);

  const getStatusColor = (status: string) => {
    switch (status.toLowerCase()) {
      case 'paid':
        return 'success';
      case 'pending':
        return 'warning';
      case 'overdue':
        return 'error';
      default:
        return 'default';
    }
  };

  return (
    <div>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 3 }}>
        <Typography variant="h4">Statements of Account</Typography>
        <Button variant="contained" color="primary">
          Generate New Statement
        </Button>
      </Box>

      <TableContainer component={Paper}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>Statement ID</TableCell>
              <TableCell>Member ID</TableCell>
              <TableCell>Statement Date</TableCell>
              <TableCell>Due Date</TableCell>
              <TableCell>Total Amount</TableCell>
              <TableCell>Paid Amount</TableCell>
              <TableCell>Status</TableCell>
              <TableCell>Actions</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {statements.map((statement) => (
              <TableRow key={statement.id}>
                <TableCell>{statement.id}</TableCell>
                <TableCell>{statement.member_id}</TableCell>
                <TableCell>{new Date(statement.statement_date).toLocaleDateString()}</TableCell>
                <TableCell>{new Date(statement.due_date).toLocaleDateString()}</TableCell>
                <TableCell>${statement.total_amount.toFixed(2)}</TableCell>
                <TableCell>${statement.paid_amount.toFixed(2)}</TableCell>
                <TableCell>
                  <Chip
                    label={statement.status}
                    color={getStatusColor(statement.status) as any}
                    size="small"
                  />
                </TableCell>
                <TableCell>
                  <Button size="small" color="primary">
                    View
                  </Button>
                  <Button size="small" color="secondary">
                    Send
                  </Button>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </div>
  );
};

export default Statements;
